import React, { Component, Suspense } from "react";
import "../Styles/CreateMeal.css"
import { wait, Snackbar, snackbarHandle } from "../dependencies";
import { S } from "selector_dom"
import { userData, data, storeData } from "../Data";
import { updateRenderState } from "..";

import NavicationBar from "../NavicationBar/NavicationBar";
const FavoritePage = React.lazy(() => import("../NavicationBar/FavoritePage"));
const ItemCard = React.lazy(() => import("../Foods/ItemCard"));
const SearchPage = React.lazy(() => import("../SearchPage/SearchPage"));

let tmpOldName;
export default class CreateMeal extends Component {
  componentDidMount() {
    this.props.doAnimation ? this.introPageTransition() : this.introPageFadeTransition();

    if (this.props.oldName) tmpOldName = this.props.oldName;
    this.addProgressBars();
    S("#CreateMealNameInputContainer input").val(userData.favorite.tmpMeal.mealName);
  }

  getData = () => {
    const dataArray = userData.favorite.tmpMeal.mealContent;
    if (dataArray.length > 0) {
      return dataArray.map((e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] })));
    } else {
      return undefined;
    }
  };

  calcNutrition = (n) => {
    const dataArray = this.getData();
    if (dataArray) {
      return dataArray.map((e) => (e[n] * (e.value * e.input)) / 100).reduce((a, b) => a + b);
    } else {
      return 0;
    }
  };

  renderMealCards = () => {
    const dataArray = this.getData();
    if (dataArray) {
      return dataArray.map((e) => (
        <Suspense fallback="" key={Math.random() * 100}>
          <ItemCard
            key={e.id + Math.random() * 1000}
            name={e.name}
            kcal={Math.round((e.kcal / 100) * (e.value * e.input))}
            serving={e.value === 1 ? e.input + " غرام " : e.input + "  " + e.serving.filter((el) => el[1] === e.value)[0][0]}
            id={e.id}
            brand={e.brand}
            description={e.description}
            value={e.value}
            input={e.input}
          />
        </Suspense>
      ));
    }
  };

  introPageTransition = async () => {
    const page = document.getElementById("CreateMealPage");
    const pageHeader = document.getElementById("CreateMealHeader");

    page.style.cssText = `position: fixed; bottom: 50%; left: 50%; opacity: 0; transform: translate(-50%, 100%); transition: bottom 200ms`;
    pageHeader.style.position = "relative";

    await wait(10);
    page.style.bottom = "100%";
    page.style.opacity = "1";

    await wait(200);
    page.removeAttribute("style");
    pageHeader.removeAttribute("style");
  };

  introPageFadeTransition = async () => {
    const page = document.getElementById("CreateMealPage");
    page.style.opacity = "0";
    page.style.transition = "opacity 350ms";
    await wait(20);
    page.style.opacity = "1";
    await wait(350);
    page.removeAttribute("style");
  };

  proteinPercent = () =>
    Math.min(Math.max(Math.round(((this.calcNutrition("protein") * 4) / this.calcNutrition("kcal")) * 100), 0), 100);
  fatPercent = () => Math.min(Math.max(Math.round(((this.calcNutrition("fat") * 9) / this.calcNutrition("kcal")) * 100), 0), 100);
  carbPercent = () =>
    Math.min(Math.max(Math.round(((this.calcNutrition("carb") * 4) / this.calcNutrition("kcal")) * 100), 0), 100);
  CalcCarbPercent = () =>
    this.carbPercent() > 15 ? Math.min(Math.max(100 - (this.fatPercent() + this.proteinPercent()), 0), 100) : this.carbPercent();

  addProgressBars = () => {
    S("#CreateMealNutritionInfoCrabsPercent").progress({
      input: this.CalcCarbPercent(),
      strokeColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      strokeBackgroundColor: "var(--background-gray)",
      strokeLinecap: "none",
    })
    S("#CreateMealNutritionInfoProteinPercent").progress({
      input: this.proteinPercent(),
      strokeColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      strokeBackgroundColor: "var(--background-gray)",
      strokeLinecap: "none",
    })
    S("#CreateMealNutritionInfoFatPercent").progress({
      input: this.fatPercent(),
      strokeColor: "var(--text-kinda-black)",
      textColor: "var(--text-kinda-black)",
      strokeBackgroundColor: "var(--background-gray)",
      strokeLinecap: "none",
    })
    const carbsTitle = document.createElement("p");
    const proteinTitle = document.createElement("p");
    const fatTitle = document.createElement("p");
    carbsTitle.classList.add("CreateMealPercentTitles");
    proteinTitle.classList.add("CreateMealPercentTitles");
    fatTitle.classList.add("CreateMealPercentTitles");
    carbsTitle.innerHTML = "الكربوهيدرات";
    proteinTitle.innerHTML = "البروتين";
    fatTitle.innerHTML = "الدهون";
    document.getElementById("CreateMealNutritionInfoCrabsPercent").append(carbsTitle);
    document.getElementById("CreateMealNutritionInfoProteinPercent").append(proteinTitle);
    document.getElementById("CreateMealNutritionInfoFatPercent").append(fatTitle);
  };

  backButtonHandle = () => window.history.back();

  createMealButtonHandle = () => {
    const [name, content] = [userData.favorite.tmpMeal.mealName.toLowerCase(), userData.favorite.tmpMeal.mealContent];
    let isNameExist = false;
    for (let i in userData.favorite.meals) if (userData.favorite.meals[i].mealName.toLowerCase() === name) isNameExist = true;

    if (name.length > 0 && content.length > 0 && !isNameExist) {
      userData.favorite.meals.unshift({ mealName: name.toString(), content: [...content] });
      userData.favorite.tmpMeal.mealName = "";
      userData.favorite.tmpMeal.mealContent = [];
      setTimeout(() => {
        window.history.pushState("", "", "/favorites");
        updateRenderState([
          <Suspense fallback="" key={Math.random() * 100}>
            <FavoritePage key={Math.random() * 100} page={2} />
          </Suspense>,
          <NavicationBar key={Math.random() * 100} />,
        ]);
      }, 100);
      this.props.goBackOnce ? window.history.go(-1) : window.history.go(-2);
    } else if (isNameExist) {
      S("#CreateMealNameInputContainer").css({borderColor: "#ff9800"});
      snackbarHandle("هذا الاسم موجود بالفعل", "warning");
      window.scrollTo({ top: 0 });
    } else if (name.length === 0) {
      S("#CreateMealNameInputContainer").css({borderColor: "#f44336"});
      snackbarHandle("ادخل اسم الوجبة", "error");
      window.scrollTo({ top: 0 });
    } else if (content.length === 0) {
      S("#addFoodToMeal").css({borderColor: "#f44336"});
      snackbarHandle("قم بإضافة طعام إلى الوجبة", "error");
    }

    storeData();
  };

  createMealEditModeButtonHandle = () => {
    const [name, oldName, content, data] = [
      userData.favorite.tmpMeal.mealName.toLowerCase(),
      this.props.oldName ? this.props.oldName.toLowerCase() : tmpOldName.toLowerCase(),
      userData.favorite.tmpMeal.mealContent,
      userData.favorite.meals,
    ];
    let doNameExist = false;
    for (let i in data)
      if (data[i].mealName.toLowerCase() === name && data[i].mealName.toLowerCase() !== oldName) doNameExist = true;

    if (name.length > 0 && content.length > 0 && !doNameExist) {
      const getIndex = data.indexOf(data.filter((e) => e.mealName === oldName)[0]);
      data[getIndex].content = [...content];
      data[getIndex].mealName = name;
      window.history.go(-2);
    } else if (doNameExist) {
      S("#CreateMealNameInputContainer").css({borderColor: "#ff9800"});
      snackbarHandle("هذا الاسم موجود بالفعل", "warning");
      window.scrollTo({ top: 0 });
    } else if (name.length === 0) {
      S("#CreateMealNameInputContainer").css({borderColor: "#f44336"});
      snackbarHandle("ادخل اسم الوجبة", "error");
      window.scrollTo({ top: 0 });
    } else if (content.length === 0) {
      S("#addFoodToMeal").css({borderColor: "#f44336"});
      snackbarHandle("قم بإضافة طعام إلى الوجبة", "error");
    }
    storeData();
  };

  addFoodToMealHandle = () => {
    updateRenderState(
      <Suspense fallback="" key={Math.random() * 100}>
        <SearchPage key={Math.random() * 100} doAnimation={true} />
      </Suspense>
    );
    if (this.props.editMode) {
      window.history.pushState("", "", "/EditMeal/search");
    } else {
      window.history.pushState("", "", "/CreateNewMeal/search");
    }
  };

  mealNameInputOnChangeHandle = (e) => (userData.favorite.tmpMeal.mealName = e.target.value);

  render() {
    return (
      <div id="CreateMealPage">
        <Snackbar />
        <header id="CreateMealHeader">
          <svg id="CreateMealBack" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" onClick={this.backButtonHandle}>
            <path d="M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z" />
          </svg>
          <p id="CreateMealHeaderTitle">{this.props.editMode ? "تعديل الوجبة" : "إنشاء وجبة جديدة"}</p>
        </header>

        <div id="CreateMealNameInput">
          <div id="CreateMealNameInputContainer">
            <p>ادخــل اســم الــوجــبــة</p>
            <input
              type="text"
              placeholder="اســم الــوجــبــة"
              onClick={(e) => {
                S("#CreateMealNameInputContainer").removeAttr("style");
              }}
              onChange={this.mealNameInputOnChangeHandle}
            />
          </div>
          <p id="addFoodToMeal" onClick={this.addFoodToMealHandle}>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
              <path d="M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z" />
            </svg>
            إضـافـة طـعـام إلـى الـوجـبـة
          </p>
        </div>
        <p id="CreateMealDone" onClick={this.props.editMode ? this.createMealEditModeButtonHandle : this.createMealButtonHandle}>
          {this.props.editMode ? "حفظ التغيرات" : "إنـشـاء وحـفـظ الـوجـبـة"}
        </p>
        <div id="CreateMealCards">{this.renderMealCards()}</div>
        <div id="CreateMealNutritionInfoContainer">
          <p id="CreateMealNutritionInfoTitle">المعلومات الغذائية</p>
          <div id="CreateMealNutritionInfoPercentContainer">
            <div id="CreateMealNutritionInfoCrabsPercent"></div>
            <div id="CreateMealNutritionInfoProteinPercent"></div>
            <div id="CreateMealNutritionInfoFatPercent"></div>
          </div>
          <div id="CreateMealNutritionInfoDetail">
            <div id="CreateMealNutritionInfoTitles">
              <p className="CreateMealNutritionInfoMain">السعرات الحرارية</p>
              <p className="CreateMealNutritionInfoMain">البروتين</p>
              <p className="CreateMealNutritionInfoMain">الكربوهيدرات</p>
              <p className="CreateMealNutritionInfoSub">الألياف</p>
              <p className="CreateMealNutritionInfoSub">السكر</p>
              <p className="CreateMealNutritionInfoMain">الدهون</p>
              <p className="CreateMealNutritionInfoSub">الدهون المشبعة</p>
              <p className="CreateMealNutritionInfoSub">الدهون الغير مشبعة</p>
              <p className="CreateMealNutritionInfoMain">آخر</p>
              <p className="CreateMealNutritionInfoSub">الكوليسترول</p>
              <p className="CreateMealNutritionInfoSub">الصوديوم</p>
              <p className="CreateMealNutritionInfoSub">البوتاسيوم</p>
            </div>
            <div id="CreateMealNutritionInfoValues">
              <p className="CreateMealNutritionInfoMain">{Number(this.calcNutrition("kcal").toFixed(1))}</p>
              <p className="CreateMealNutritionInfoMain">{Number(this.calcNutrition("protein").toFixed(1))} غ</p>
              <p className="CreateMealNutritionInfoMain">{Number(this.calcNutrition("carb").toFixed(1))} غ</p>
              <p className="CreateMealNutritionInfoSub">{Number(this.calcNutrition("fiber").toFixed(1))} غ</p>
              <p className="CreateMealNutritionInfoSub">{Number(this.calcNutrition("sugar").toFixed(1))} غ</p>
              <p className="CreateMealNutritionInfoMain">{Number(this.calcNutrition("fat").toFixed(1))} غ</p>
              <p className="CreateMealNutritionInfoSub">{Number(this.calcNutrition("saturatedFat").toFixed(2))} غ</p>
              <p className="CreateMealNutritionInfoSub">{Number(this.calcNutrition("unsaturatedFat").toFixed(2))} غ</p>
              <p className="CreateMealNutritionInfoMain" style={{ visibility: "hidden" }}>
                ......
              </p>
              <p className="CreateMealNutritionInfoSub">{this.calcNutrition("cholesterol").toFixed(0)} ملغ</p>
              <p className="CreateMealNutritionInfoSub">{this.calcNutrition("sodium").toFixed(0)} ملغ</p>
              <p className="CreateMealNutritionInfoSub">{this.calcNutrition("potassium").toFixed(0)} ملغ</p>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
